


$(document).ready(function() {
	(function(){

		const scrollTop = $(window).scrollTop();
		const Window = $(window);
		const htmlAndBody = $('html,body');

  		const nav = $('nav');

		const bigBox = $('.big-box');
		const nameBox = $('.name-box');
		const whiteBoxHero = $('.white-box-wrap-hero');
		const arrowWrapHero = $('.arrow-wrap-hero');
		const arrowOverlayHero = $('.arrow-overlay-hero');
		
		const quoterWrap = $('.quoter-wrap');
		const quoteIcon = $('.quote-icon-wrap');
		
		const aboutGoldenLine = $('.about-golden-line');
		const aboutTitle = $('.about-title-h1');

		const servicesTitle = $('.services-title-h1');
		const servicesGoldenLine = $('.services-golden-line');

		const contactDesign = $('.contact-wrap-design');
		const contactForm = $('.contact-form');
		const contactSection = $('.contact-s');
		const submitButton = $('.submit-button');

		const projectsTitle = $('.projects-title-h1');
		const projectsTitleWrap = $('.projects-title-wrap');
		const projectsGoldenLine = $('.projects-golden-line');

		const getInTouchP = $('p.getintouch-p');
		const getInTouchWrap = $('.getintouch-h1-wrap');

		const contactTitle = $('.contact-title-h1');
		const contactGoldenLine = $('.contact-golden-line');

		const blockQuote = $('blockquote.quote');

		const designImageWrap = $('.design-image-wrap');
		const designImage = $('.design-image');
		const codeImageWrap = $('.code-image-wrap');

		const hamburger = $('.hamburger-wrap');
		const allBars = $('a.hamburger-wrap > div');
		const bar1 = $('.bar-1');
		const bar2 = $('.bar-2');
		const bar3 = $('.bar-3');

		const barUpDown = $('*[data-up-down="updown"]');

		const sidenav = $('.sidenav');

		const navLink = $('.nav-link');
		const navBg = $('.nav-bg');
		const navSpan = $('.nav-span');

		const sidenavLink = $('.sidenav-menu-link');

		const inputName = $('.input-name');
		const inputEmail = $('.input-email');
		const textareaMessage = $('.textarea-message');

		const popupWrap = $('.popup-wrap');

		const successPopupContentWrap = $('*[data-popup-content="success"]');
		const successPopupTitleH1Outer = $('*[data-popup-h1-outer="success"]');
		const successPopupWhiteboxContent = $('*[data-popup-whitebox="success"]');
		const successParagraph = $('*[data-popup-p="success"]');
		const successTitle = $('*[data-popup-h1="success"]');

		const failedPopupContentWrap = $('*[data-popup-content="failed"]');
		const failedPopupTitleH1Outer = $('*[data-popup-h1-outer="failed"]');
		const failedPopupWhiteboxContent = $('*[data-popup-whitebox="failed"]');
		const failedParagraph = $('*[data-popup-p="failed"]');
		const failedTitle = $('*[data-popup-h1="failed"]');

		const contactDetailsWrap = $('.contact-details-wrap');

		const viewportWidth = $('.viewport-width');
		
		$('.quote-s').slick({
		  centerMode: false,
		  arrows: true,
		  infinite: false,
		  speed: 500,
		  slidesToShow: 1,
		  centerMode: true,
		  variableWidth: true,
		  adaptiveHeight: true,
		  dots: true,
		  nextArrow: '<button class="slick-next slick-arrow" aria-label="Next" type="button" aria-disabled="false" style=""><i class="slider-arrow-next"></i></button>',
		  prevArrow: '<button class="slick-prev slick-arrow" aria-label="Previous" type="button" aria-disabled="false" style=""><i class="slider-arrow-prev"></i></button>',
		  dotsClass: 'slider-dots',
		  // customPaging: function(slider, pageIndex) {
		  // 	return $('<button></button').text(slider.$slider.data('buttonlabel');
		  // },
		  customPaging: function(slider, i) {
		  	return $('<button type="button" class="slider-dot" />');
		  },
		  responsive: [
		    {
		      breakpoint: 768,
		      settings: {
		        arrows: true,
		        centerMode: true,
		        infinite: false,
		  		speed: 500,
		  		slidesToShow: 1,
		  		centerMode: true,
		  		variableWidth: true,
		        
		      }
		    },
		    {
		      breakpoint: 480,
		      settings: {
		        arrows: true,
		        centerMode: true,
		        slidesToShow: 1,
		        
		      }
		    }
		  ]
		});

		
		const quoteAppear = () => {
			quoterWrap.animate({"opacity":"1"}, 800);
			quoteIcon.animate({"opacity":"1"}, 800);
			blockQuote.animate({"opacity":"1"}, 800);
		};

		//arrow click hero
		arrowOverlayHero.on('click', quoteAppear);
		

		
		const viewport = function (){
	    // Get the dimensions of the viewport
	    const width = $(window).width();
	    const height = $(window).height();

	    $('.viewport-width').html(width + 'px');      // Display the width
	      
		};
		   // When the page first loads
		$(window).resize(viewport);    

		Window.on('beforeunload', function() {
		    Window.scrollTop(0);
		    window.location.href=window.location.href;
		});

		contactForm.validate({
			errorClass: 'errorAlert',
			rules: {
				_replyto: {
					required: true,
					email: true
				},
				name: {
					required: true,

				}
			},
			
			messages: {
				_replyto:{
					required: "Please enter your email adress.",
					email: 'Please enter a <i>valid</i> email adress.'
				},

				name: {
					required: "Please enter your name."
				}
				
			},
			
			onfocusout: function(element) {
            	this.element(element);
        	},

		});
	

		contactForm.on("submit", function(e) {
			e.preventDefault();

			if($(this).valid()) {
				$.ajax({
			        url: "https://formspree.io/max@colesmedia.se", 
			        method: "POST",
			        data: {
			        	name: inputName.val(),
			        	email: inputEmail.val(),
			        	message: textareaMessage.val()
			        },
			        dataType: "json"
			    })
				.done(thanks)
				.fail(failed);
			} 
			return false;
		});

		inputName.keypress(function(e) {
			
			if(e.which == 13) {
				e.preventDefault();
				return;
			}
		});
		inputEmail.keypress(function(e) {
			
			if(e.which == 13) {
				e.preventDefault();
				return;
			}
		});

		//popup
		
		
		const thanks = function (){

			popupWrap.css({"z-index":"5"});
			
			submitButton.addClass("inactive");

			const name = inputName.val();
			const email = inputEmail.val();

			const msgSuccess = `Thank you <i>${name}</i> for taking the time to get in touch. Your message was successfully sent from the email adress <i>${email}</i>.`;

			successParagraph.html(msgSuccess);
			successTitle.text("Success!");


			setTimeout(function(){
				contactForm.css({filter:"blur(1px)"});
				contactTitle.css({filter:"blur(1px)"});
				contactDetailsWrap.css({filter:"blur(1px)"});

				successPopupContentWrap.animate({width: "100%"}, 600, "swing");

				setTimeout(function(){
					popupTitleShow();
				}, 400);

				const popupTitleShow = function(){
					successPopupTitleH1Outer.animate({width:"100%"}, 600, "swing", closeIconShow);
				};

				const closeIconShow = function(){
					successPopupWhiteboxContent.animate({height:"100%"}, 400, "swing");
				};

			}, 200);
			
			



			const closePopup = function(e){
				e.preventDefault();
				
				successPopupWhiteboxContent.addClass("inactive");

				setTimeout(function(){
					closeIconHide();

				}, 200);

				const closeIconHide = function(){
					successPopupWhiteboxContent.animate({height:"0%"}, 400, "swing", popupTitleHide);
				};

				const popupTitleHide = function(){

					setTimeout(function(){
						successPopupContentWrapHide();
					}, 300);

					successPopupTitleH1Outer.animate({width:"0%"}, 600, "swing");
				};

				const successPopupContentWrapHide = function(){
					successPopupContentWrap.animate({width: "0%"}, 600, "swing", removeBlur);
				};

				const removeBlur = function(){
					contactForm.css({filter:"blur(0px)"});
					contactTitle.css({filter:"blur(0px)"});
					contactDetailsWrap.css({filter:"blur(0px)"});

					successPopupWhiteboxContent.removeClass("inactive");
					submitButton.removeClass("inactive");
					popupWrap.css({"z-index":"-1"});
					location.reload();
				};


			};

			successPopupWhiteboxContent.on('click', closePopup);

			inputName.val('');
			inputEmail.val('');
			textareaMessage.val('');

			
			

			
		};

		const failed = function (){

			popupWrap.css({"z-index":"5"});

			submitButton.addClass("inactive");

			const msgFailed = `Apparently something went wrong when trying to submit your message. Please refresh this page and try again.`;

			successParagraph.text(msgFailed);
			successTitle.text("Oops!");

			

			setTimeout(function(){
				contactForm.css({filter:"blur(1px)"});
				contactTitle.css({filter:"blur(1px)"});
				contactDetailsWrap.css({filter:"blur(1px)"});

				failedPopupContentWrap.animate({width: "100%"}, 600, "swing");

				setTimeout(function(){
					popupTitleShow();
				}, 400);

				const popupTitleShow = function(){
					failedPopupTitleH1Outer.animate({width:"100%"}, 600, "swing", closeIconShow);
				};

				const closeIconShow = function(){
					failedPopupWhiteboxContent.animate({height:"100%"}, 400, "swing");
				};

			}, 200);
			
			



			const closePopup = function(e){

				e.preventDefault();
				failedPopupWhiteboxContent.addClass("inactive");

				setTimeout(function(){
					closeIconHide();

				}, 200);

				const closeIconHide = function(){
					failedPopupWhiteboxContent.animate({height:"0%"}, 400, "swing", popupTitleHide);
				};

				const popupTitleHide = function(){

					setTimeout(function(){
						failedPopupContentWrapHide();
					}, 300);

					failedPopupTitleH1Outer.animate({width:"0%"}, 600, "swing");
				};

				const failedPopupContentWrapHide = function(){
					failedPopupContentWrap.animate({width: "0%"}, 600, "swing", removeBlur);
				};

				const removeBlur = function(){
					contactForm.css({filter:"blur(0px)"});
					contactTitle.css({filter:"blur(0px)"});
					contactDetailsWrap.css({filter:"blur(0px)"});

					failedPopupWhiteboxContent.removeClass("inactive");
					submitButton.removeClass("inactive");
					popupWrap.css({"z-index":"0"});
				};
			};

			failedPopupWhiteboxContent.on('click', closePopup);
			

		};





		//LINK TAGS SCROLL 


		$("a[href^=\\#]").click(function(e) { 
			e.preventDefault(); 
			const dest = $(this).attr('href'); 
	
			htmlAndBody.animate({ scrollTop: $(dest).offset().top - 100 }, 'slow'); });


		//HERO ANIMATIONS


		function hero(){
			
				const bigBoxAnimate = function(){

					bigBox.animate({"width":"100%"}, 800, "swing", function(){
						
					arrowWrapHero.css({"opacity": "1"});
				}); 
			};

				const nameBoxAnimate = function(){ nameBox.animate({"width":"100%"}, 800, "swing", function(){
						nav.animate({"margin":"0px auto 0px auto"}, 600, "swing");
					}); 
				};
				const whiteBoxHeroAnimate = function(){ whiteBoxHero.animate({"bottom":"-10px"}, 600, "swing"); };

				setTimeout(function(){

					bigBoxAnimate(); 

				}, 600);

				setTimeout(function(){

					nameBoxAnimate();

				}, 900);

				setTimeout(function(){

					whiteBoxHeroAnimate();

				}, 1700);



		}

		hero();

		//SIDENAV

		sidenavLink.click(function(e){
			e.preventDefault();

			if (hamburger.height() === 40) {
				hamburger.addClass("inactive");

					allBars.stop().animate({height: "4px"}, 200, "linear", function(){
						moveUp();
						sidenavShrink();
					});
						
					const sidenavShrink = function(){
						sidenav.stop().animate({width: "-7%"}, 400, "swing");
					};
					
					const moveUp = function(){

						hamburger.stop().animate({"top": "0px"}, 600, "swing", function(){

							bar1Top();
							bar3Top();
							classesReverse();

						});

					};

					const bar1Top = function(){
						bar1.stop().animate({top: "0px"}, 200, "swing");

					};

					const bar3Top = function(){
						bar3.stop().animate({top: "35px"}, 200, "swing");
					};

					const classesReverse = function(){
						hamburger.removeClass("inactive");

						hamburger.removeClass("open");
						hamburger.addClass("closed");
					};
			

				} else {

					hamburger.addClass("inactive");

					allBars.stop().animate({height: "2px"}, 200, "linear", function(){
						moveUp();
						sidenavShrink();
					});
						
					const sidenavShrink = function(){
						sidenav.stop().animate({width: "-7%"}, 400, "swing");
					};
					
					const moveUp = function(){

						hamburger.stop().animate({"top": "0px"}, 600, "swing", function(){

							bar1Top();
							bar3Top();
							classesReverse();

						});

					};

					const bar1Top = function(){
						bar1.stop().animate({top: "0px"}, 200, "swing");

					};

					const bar3Top = function(){
						bar3.stop().animate({top: "22px"}, 200, "swing");
					};

					const classesReverse = function(){
						hamburger.removeClass("inactive");

						hamburger.removeClass("open");
						hamburger.addClass("closed");
					};
				}	


		});

		hamburger.click(function(e){

			e.preventDefault();


			if (hamburger.height() === 40) {

				if (hamburger.hasClass("closed") ) {

					hamburger.addClass("inactive");

					barUpDown.stop().animate({top: "17px"}, 400, "swing", function(){
						hamburgerMove();
						sidenavExpand();
						barHeight();
					});

					const hamburgerMove = function(){
						hamburger.stop().animate({"top": "300px"}, 600, "swing", function(){
							
							classes();
						
						});
					};

					const sidenavExpand = function(){
						sidenav.stop().animate({width: "107%"}, 400, "swing");
					};

					const barHeight = function(){
						allBars.stop().animate({height: "6px"}, 200, "linear");
					};

					const classes = function(){

						hamburger.removeClass("closed");
						hamburger.removeClass("inactive");

						hamburger.addClass("open");
					};

				} else {

					hamburger.addClass("inactive");

					allBars.stop().animate({height: "4px"}, 200, "linear", function(){
						moveUp();
						sidenavShrink();
					});
						
					const sidenavShrink = function(){
						sidenav.stop().animate({width: "-7%"}, 400, "swing");
					};
					
					const moveUp = function(){

						hamburger.stop().animate({"top": "0px"}, 600, "swing", function(){

							bar1Top();
							bar3Top();
							classesReverse();

						});

					};

					const bar1Top = function(){
						bar1.stop().animate({top: "0px"}, 200, "swing");

					};

					const bar3Top = function(){
						bar3.stop().animate({top: "35px"}, 200, "swing");
					};

					const classesReverse = function(){
						hamburger.removeClass("inactive");

						hamburger.removeClass("open");
						hamburger.addClass("closed");
					};

				}

			} else {

				if (hamburger.hasClass("closed") ) {

					hamburger.addClass("inactive");

					barUpDown.stop().animate({top: "11px"}, 400, "swing", function(){
						hamburgerMove();
						sidenavExpand();
						barHeight();
					});

					const hamburgerMove = function(){
						hamburger.stop().animate({"top": "185px"}, 600, "swing", function(){
							
							classes();
						
						});
					};

					const sidenavExpand = function(){
						sidenav.stop().animate({width: "107%"}, 400, "swing");
					};

					const barHeight = function(){
						allBars.stop().animate({height: "4px"}, 200, "linear");
					};

					const classes = function(){

						hamburger.removeClass("closed");
						hamburger.removeClass("inactive");

						hamburger.addClass("open");
					};

				} else {

					hamburger.addClass("inactive");

					allBars.stop().animate({height: "2px"}, 200, "linear", function(){
						moveUp();
						sidenavShrink();
					});
						
					const sidenavShrink = function(){
						sidenav.stop().animate({width: "-7%"}, 400, "swing");
					};
					
					const moveUp = function(){

						hamburger.stop().animate({"top": "0px"}, 600, "swing", function(){

							bar1Top();
							bar3Top();
							classesReverse();

						});

					};

					const bar1Top = function(){
						bar1.stop().animate({top: "0px"}, 200, "swing");

					};

					const bar3Top = function(){
						bar3.stop().animate({top: "22px"}, 200, "swing");
					};

					const classesReverse = function(){
						hamburger.removeClass("inactive");

						hamburger.removeClass("open");
						hamburger.addClass("closed");
					};

				}

			}				
				
				
		});

		//SCROLL ANIMATIONS
		
		const scroll = function(){

			function intoView(elem)
				{
			    const docViewTop = Window.scrollTop();
			    const docViewBottom = docViewTop + Window.height();

			    const elemTop = $(elem).offset().top;
			    const elemBottom = elemTop + $(elem).height();

			    return ((elemBottom <= docViewBottom) && (elemTop >= docViewTop));
			}


			const windowScroll = function() {
				//made second scrollTop because it changes context here
				const scrollTop2 = $(window).scrollTop();
				//title animations

				if (scrollTop2 >= 100) {
					quote();
				}

				if ( intoView(contactDesign) === true) {
					navAnchor("#services");

					navAnchorOff("#about");
					navAnchorOff("#contact");
					navAnchorOff("#projects");

					services();

				} else {
					navAnchorOff("#services");
				}

				if ( intoView(aboutTitle) === true ) {
					navAnchor("#about");

					navAnchorOff("#services");
					navAnchorOff("#contact");
					navAnchorOff("#projects");

					about();
				
				}

				if (intoView(projectsTitleWrap) === true) {
					navAnchor("#projects");

					navAnchorOff("#about");
					navAnchorOff("#contact");
					navAnchorOff("#services");

					projects();

				}

				if ( intoView(getInTouchP) === true ) {
					touch();
				}

				if ( intoView(contactTitle) === true ) {
					navAnchor("#contact");

					navAnchorOff("#about");
					navAnchorOff("#projects");
					navAnchorOff("#services");

					contact();
				}


			};

			Window.on('scroll', windowScroll);



		};

		const navAnchor = function(id){

			const bgNav = $('.nav-link[href="'+ id +'"] > .nav-bg');
			const spanNav = $('.nav-link[href="'+ id +'"] > .nav-span');

			spanNav.addClass("animate-nav");
			bgNav.addClass("animate-nav");

		};

		const navAnchorOff = function(id){

			const bgNav = $('.nav-link[href="'+ id +'"] > .nav-bg');
			const spanNav = $('.nav-link[href="'+ id +'"] > .nav-span');

		
			spanNav.removeClass("animate-nav");
			bgNav.removeClass("animate-nav");


		};


		const quote = function() {
			quoterWrap.animate({"opacity":"1"}, 800);
			quoteIcon.animate({"opacity":"1"}, 800);
			blockQuote.animate({"opacity":"1"}, 800);
		};

		const services = function(){
			servicesGoldenLine.animate({"left":"0", "opacity":"1"}, 800);
			servicesTitle.animate({"left":"0", "opacity":"1"}, 800);
		};

		const about = function(){
			aboutGoldenLine.animate({"left":"0", "opacity":"1"}, 800);
			aboutTitle.animate({"left":"0", "opacity":"1"}, 800);
		};

		const projects = function(){
			projectsGoldenLine.animate({"left":"0", "opacity":"1"}, 800);
			projectsTitle.animate({"left":"0", "opacity":"1"}, 800);
		};

		const touch = function(){
			getInTouchWrap.animate({"width":"100%"}, 800);
			
		};

		const contact = function(){
			contactGoldenLine.animate({"left":"0", "opacity":"1"}, 800);
			contactTitle.animate({"left":"0", "opacity":"1"}, 800);
		};

		scroll();

	})();
	
});


