<template>
	<div class="projects-wrapper">

		<div v-if="!projects"></div>
		<div v-else v-for="project in projects">

			<div class="project-content-wrap">
				<div class="project-text-wrap">
					<h2 class="project-h2">
						{{project.title}}
					</h2>
					<p class="project-p">
						{{project.content}}
					</p>

					<a class="project-button-wrap" :href="project.href" target="_blank">
						<span class="project-visit-span">
							VISIT
						</span>
						<i class="project-visit-wrap">
							<img src="./assets/icons/visit.svg" alt="" class="project-redirect-image">
						</i>
					</a>
				</div>

				<div class="project-showcase-wrap">

					<div class="project-bg" :style="{ 'background-color': project.bgColor }"></div>
					
					<div class="project-mockup-wrap">
						<img :src="project.src" :srcset="project.srcset" alt="" class="project-mockup-image">
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data () {
			return {
				DB_URL: 'https://colesmedia.se/data/projects.json',
				projects: ''
			}
		},
		created() {
			this.api();
		},
		methods: {
			log(d) {
				console.log(JSON.parse(JSON.stringify(d)));
			},
			makeJsonObject(data) {
				if(typeof data == 'string') {
					return JSON.parse(data)
				}
				return data
			},
			api() {
				return fetch(this.DB_URL)
				.then(response => response.json())
				.then((data) => {
					// data = this.makeJsonObject(data);

					this.log(data.projects);
					this.projects = data.projects;
					// this.$nextTick(() => {
					// 	window.scrollTo(0, 1)
					// 	window.scrollTo(0, 0)
					// })
					// this.log(this.projects)
					
					return projects;
				})
				.catch((err) => {
					console.log("something went wrong!: ", err);
				})
			}
		},
	}
</script>


